body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
        "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.ant-layout .ant-layout-sider-children {
    margin: 0 !important;
}

.ant-select {
    width: -webkit-fill-available !important;
    width: -moz-available !important;
    width: stretch !important;
}

.ant-menu.ant-menu-dark {
    background: #141414;
}

.ant-list-header {
    border-block-end: none !important;
}

.ant-menu.ant-menu-root.ant-menu-inline {
    border-inline-end: none !important;
}

.ant-card-actions {
    background: transparent !important;
}

.warning .ant-modal-content {
    border-top: 8px solid orange;
}

.ant-form-item .ant-form-item-label>label.ant-form-item-required:not(.ant-form-item-required-mark-optional)::before {
    display: none;
}

.ant-form-item .ant-form-item-label>label.ant-form-item-required:not(.ant-form-item-required-mark-optional)::after {
    color: #ff4d4f !important;
    display: inline-block;
    margin-inline-end: 4px;
    font-family: SimSun, sans-serif;
    line-height: 1;
    content: "*";
    visibility: visible !important;
}

/* tonie article search - in searchfield display none, but in dropdown displayed */
.ant-select-dropdown img {
    height: 64px !important;
    width: 64px !important;
    object-fit: cover !important;
    display: block !important;
    margin: 8px;
}

.ant-table-cell {
    padding: 8px !important;
}

/* workaround missing good responsiveness of antd tables... */

.showBigDevicesOnly {
    display: none;
}

.showMediumDevicesOnly {
    display: none;
}

.showSmallDevicesOnly {
    display: block;
}

.ant-table-cell img {
    width: 75px !important;
}

@media (min-width: 768px) {
    .showSmallDevicesOnly {
        display: none;
    }

    .ant-table-cell img {
        width: 100px !important;
    }
}

@media (min-width: 768px) and (max-width: 1199px) {
    .showMediumDevicesOnly {
        display: block;
    }
}

@media (min-width: 1200px) {
    .showBigDevicesOnly {
        display: block;
    }
}

/* workaround to set same card height within a row,
 * solution based on proposed solution in https://github.com/ant-design/ant-design/issues/40786
 * --> https://stackblitz.com/edit/react-wf9wmw-2i6mck?file=index.css,demo.tsx */
.ant-list .ant-row>div,
.ant-list .ant-row>div,
.ant-list .ant-row .ant-col:first-child {
    display: flex;
}

.ant-list .ant-row .ant-list-item {
    flex: 1 1 auto;
}

.ant-list .ant-row .ant-list-item .ant-card {
    height: 100%;
    display: flex;
    flex-direction: column;
}

.ant-card-cover {
    height: 100%;
    align-content: center;
    height: 100%;
    align-content: center;
}

/* end of workaround */